.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: $black-pearl-veil;
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
  padding-top: 145px;

  &::-webkit-scrollbar {
    display: none;
  }

  &__container {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__content {
    position: relative;
    width: 500px;
    padding: 40px;
    background-color: $deep-sky-blue-accent;
    border-radius: 3px;
    margin: 0 auto;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}

.modal__heading-wrapper {
  color: $color-default-white;
  font-family: $pt-mono;
  font-size: 16px;
  line-height: 21.5px;

  h2 {
    font-family: $roboto-condensed;
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
    margin: 0;
    margin-bottom: 15px;
  }

  p {
    margin: 0;
    margin-bottom: 40px;
  }
}

.modal__input-wrapper {
  display: grid;
  grid-template-areas:
    "username"
    "userphone"
    "question"
    "button"
    "agreement";
}

.modal__input {
  label {

    & input:not([type="checkbox"]),
    & textarea {
      background-color: $black-pearl-faded;
      border: none;
      border-radius: 3px;
      color: $alice-blue;
      width: 100%;

      &::placeholder {
        color: $color-default-white;
      }

      &:hover,
      &:focus {
        outline: none;
        box-shadow: 2px -2px 5px $black-pearl;
      }

      &:active,
      &:focus-within {
        outline: none;
        background-color: rgba($black-pearl, 0.5);
      }
    }
  }

  & input:not([type="checkbox"]) {
    padding: 15px;
  }

  & textarea {
    width: 100%;
    padding: 25px 15px 10px 15px;
  }

  &--username {
    grid-area: username;
    margin-bottom: 20px;
    width: 100%;
  }

  &--userphone {
    grid-area: userphone;
    max-width: 100%;
    margin-bottom: 20px;
  }

  &--question {
    grid-area: question;
    margin-bottom: 25px;
  }

  &--button {
    grid-area: button;
    font-family: $roboto-condensed;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    background-color: $black-pearl-accent;
    color: $color-default-white;
    border: none;
    border-radius: 3px;
    padding: 13px;
    margin-bottom: 20px;

    &:hover,
    &:focus {
      background-color: $color-default-white;
      color: $black-pearl-accent;
      opacity: .8;
    }

    &:active {
      opacity: 1;
    }
  }

  &--agreement {
    grid-area: agreement;
    color: $color-default-white;
    font-size: 14px;
    line-height: 16px;
    opacity: 0.8;

    label {
      display: flex;
      align-items: center;
      padding-top: 13px;
    }
  }

  input[type="checkbox"]:checked+.modal__agreement-mark {
    &::after {
      content: "";
    }
  }
}

.modal__agreement-mark {
  display: block;
  position: relative;
  width: 15px;
  height: 15px;
  margin-right: 10px;
  border: 1px solid $color-default-white;
  border-radius: 2px;

  &::after {
    position: absolute;
    top: 13%;
    left: 12%;
    display: block;
    width: 7px;
    height: 5px;
    border: 1px solid $color-default-white;
    border-right: none;
    border-top: none;
    transform: rotate(-45deg);
  }
}

.modal__close-button {
  position: absolute;
  width: 40px;
  height: 40px;
  right: 10%;

  cursor: pointer;

  &::after,
  &::before {
    content: "";
    position: absolute;
    top: 47%;
    left: 17%;
    display: block;
    width: 28px;
    height: 1px;
    background-color: $color-default-white;
    transform: rotate(45deg);
  }

  &::before {
    transform: rotate(-45deg);
  }
}
