html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  min-width: 320px;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
  padding-top: 145px;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__container {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 40px;
  background-color: #00b3ff;
  border-radius: 3px;
  margin: 0 auto;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__heading-wrapper {
  color: #ffffff;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 21.5px;
}
.modal__heading-wrapper h2 {
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  margin: 0;
  margin-bottom: 15px;
}
.modal__heading-wrapper p {
  margin: 0;
  margin-bottom: 40px;
}

.modal__input-wrapper {
  display: grid;
  grid-template-areas: "username" "userphone" "question" "button" "agreement";
}

.modal__input label input:not([type=checkbox]), .modal__input label textarea {
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  color: #f2f5f6;
  width: 100%;
}
.modal__input label input:not([type=checkbox])::placeholder, .modal__input label textarea::placeholder {
  color: #ffffff;
}
.modal__input label input:not([type=checkbox]):hover, .modal__input label input:not([type=checkbox]):focus, .modal__input label textarea:hover, .modal__input label textarea:focus {
  outline: none;
  box-shadow: 2px -2px 5px #041827;
}
.modal__input label input:not([type=checkbox]):active, .modal__input label input:not([type=checkbox]):focus-within, .modal__input label textarea:active, .modal__input label textarea:focus-within {
  outline: none;
  background-color: rgba(4, 24, 39, 0.5);
}
.modal__input input:not([type=checkbox]) {
  padding: 15px;
}
.modal__input textarea {
  width: 100%;
  padding: 25px 15px 10px 15px;
}
.modal__input--username {
  grid-area: username;
  margin-bottom: 20px;
  width: 100%;
}
.modal__input--userphone {
  grid-area: userphone;
  max-width: 100%;
  margin-bottom: 20px;
}
.modal__input--question {
  grid-area: question;
  margin-bottom: 25px;
}
.modal__input--button {
  grid-area: button;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: #041427;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  padding: 13px;
  margin-bottom: 20px;
}
.modal__input--button:hover, .modal__input--button:focus {
  background-color: #ffffff;
  color: #041427;
  opacity: 0.8;
}
.modal__input--button:active {
  opacity: 1;
}
.modal__input--agreement {
  grid-area: agreement;
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.8;
}
.modal__input--agreement label {
  display: flex;
  align-items: center;
  padding-top: 13px;
}
.modal__input input[type=checkbox]:checked + .modal__agreement-mark::after {
  content: "";
}

.modal__agreement-mark {
  display: block;
  position: relative;
  width: 15px;
  height: 15px;
  margin-right: 10px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}
.modal__agreement-mark::after {
  position: absolute;
  top: 13%;
  left: 12%;
  display: block;
  width: 7px;
  height: 5px;
  border: 1px solid #ffffff;
  border-right: none;
  border-top: none;
  transform: rotate(-45deg);
}

.modal__close-button {
  position: absolute;
  width: 40px;
  height: 40px;
  right: 10%;
  cursor: pointer;
}
.modal__close-button::after, .modal__close-button::before {
  content: "";
  position: absolute;
  top: 47%;
  left: 17%;
  display: block;
  width: 28px;
  height: 1px;
  background-color: #ffffff;
  transform: rotate(45deg);
}
.modal__close-button::before {
  transform: rotate(-45deg);
}

