  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 7%;
}
@media (max-width: 1023px) {
  .catalog__list {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.catalog__link {
  position: relative;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
}
.catalog__link p {
  margin: 0;
  position: absolute;
  grid-row: 1/2;
  max-width: 185px;
  margin-top: 50px;
  margin-left: 40px;
}
@media (max-width: 1023px) {
  .catalog__link p {
    max-width: 150px;
  }
}
@media (max-width: 767px) {
  .catalog__link p {
    font-size: 18px;
    max-width: 135px;
  }
}
.catalog__link:hover, .catalog__link:focus {
  box-shadow: 0 -2px 5px #00b3ff;
}
.catalog__link:active {
  box-shadow: 0 -2px 5px #ebebeb;
}

.catalog__image {
  width: 100%;
}
.catalog__image img {
  width: 100%;
}

.catalog__link-button {
  width: 60px;
  height: 60px;
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: #00b2ff;
  border: none;
  border-radius: 3px 0 0;
}
@media (max-width: 767px) {
  .catalog__link-button {
    width: 40px;
    height: 40px;
  }
}
.catalog__link-button::before {
  content: "";
  position: absolute;
  display: block;
  width: 6px;
  height: 6px;
  border: 1px solid #ffffff;
  border-bottom: none;
  border-left: none;
  transform: rotate(45deg);
  left: 43%;
  top: 45%;
}
@media (max-width: 767px) {
  .catalog__link-button::before {
    width: 4px;
    height: 4px;
  }
}
.catalog__link-button:hover, .catalog__link-button:focus {
  background-color: #ebebeb;
}
.catalog__link-button:hover::before, .catalog__link-button:focus::before {
  border-color: #00b2ff;
}
.catalog__link-button:active {
  opacity: 0.8;
}

.form {
  width: 100%;
  padding-top: 170px;
  padding-bottom: 250px;
  background-color: #00b2ff;
  background: linear-gradient(135deg, rgba(4, 24, 39, 0.5) 0% 33%, #00b3ff 33% 100%);
  font-family: "PT Mono", "Courier New", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .form {
    padding-top: 50px;
    padding-bottom: 125px;
    background: none;
  }
}
@media (max-width: 767px) {
  .form {
    padding-bottom: 90px;
  }
}

.form__container {
  max-width: 1220px;
  margin: 0 auto;
  display: flex;
}
@media (max-width: 1023px) {
  .form__container {
    padding: 0 50px;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .form__container {
    padding: 0 25px;
  }
}
@media (max-width: 1023px) {
  .form__container form {
    width: 100%;
  }
}

.form__image {
  padding-top: 76px;
  margin-right: 115px;
}
@media (max-width: 1023px) {
  .form__image {
    display: none;
  }
}

.form__heading-wrapper h2 {
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin: 0;
  margin-bottom: 15px;
}
@media (max-width: 1023px) {
  .form__heading-wrapper h2 {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .form__heading-wrapper h2 {
    font-size: 30px;
    line-height: 42px;
    margin-bottom: 20px;
  }
}
.form__heading-wrapper p {
  margin: 0;
  margin-bottom: 35px;
  padding-left: 10px;
}
@media (max-width: 1023px) {
  .form__heading-wrapper p {
    margin-bottom: 25px;
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .form__heading-wrapper p {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 40px;
  }
}

.form__input-wrapper {
  display: grid;
  grid-template-areas: "username userphone" "question question" "button agreement";
  max-width: 580px;
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .form__input-wrapper {
    width: 100%;
    max-width: 923px;
  }
}
@media (max-width: 767px) {
  .form__input-wrapper {
    grid-template-areas: "username" "userphone" "question" "button" "agreement";
  }
}

.form__input label input:not([type=checkbox]), .form__input label textarea {
  background-color: rgba(4, 20, 39, 0.15);
  line-height: 22px;
  border: none;
  border-radius: 3px;
  color: #f2f5f6;
  width: 100%;
}
.form__input label input:not([type=checkbox])::placeholder, .form__input label textarea::placeholder {
  color: #ffffff;
}
.form__input label input:not([type=checkbox]):hover, .form__input label input:not([type=checkbox]):focus, .form__input label textarea:hover, .form__input label textarea:focus {
  outline: none;
  box-shadow: 2px -2px 5px #041827;
}
.form__input label input:not([type=checkbox]):active, .form__input label input:not([type=checkbox]):focus-within, .form__input label textarea:active, .form__input label textarea:focus-within {
  outline: none;
  background-color: rgba(4, 24, 39, 0.5);
}
.form__input input:not([type=checkbox]) {
  padding: 15px;
}
.form__input textarea {
  width: 100%;
  padding: 35px 15px 10px 15px;
}
@media (max-width: 767px) {
  .form__input textarea {
    padding-top: 25px;
  }
}
.form__input--username {
  grid-area: username;
  margin-bottom: 35px;
  width: 275px;
}
@media (max-width: 1023px) {
  .form__input--username {
    min-width: 320px;
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .form__input--username {
    width: 100%;
    min-width: 270px;
    margin-bottom: 20px;
  }
}
.form__input--userphone {
  grid-area: userphone;
  max-width: 275px;
}
@media (max-width: 1023px) {
  .form__input--userphone {
    min-width: 320px;
  }
}
@media (max-width: 767px) {
  .form__input--userphone {
    width: 100%;
    min-width: 270px;
    max-width: none;
    margin-bottom: 20px;
  }
}
.form__input--question {
  grid-area: question;
  margin-bottom: 35px;
}
@media (max-width: 767px) {
  .form__input--question {
    margin-bottom: 30px;
  }
}
.form__input--button {
  grid-area: button;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: #041427;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  padding: 13px;
  margin-right: 10px;
}
@media (max-width: 767px) {
  .form__input--button {
    margin-right: 0;
    margin-bottom: 25px;
  }
}
.form__input--button:hover, .form__input--button:focus {
  background-color: #ffffff;
  color: #041427;
  opacity: 0.8;
}
.form__input--button:active {
  opacity: 1;
}
.form__input--agreement {
  grid-area: agreement;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.8;
}
.form__input--agreement label {
  display: flex;
  align-items: center;
  padding-top: 13px;
}
@media (max-width: 767px) {
  .form__input--agreement label {
    padding: 0;
  }
}
.form__input input[type=checkbox]:checked + .form__agreement-mark::after {
  content: "";
}

.form__agreement-mark {
  display: block;
  position: relative;
  width: 21px;
  height: 15px;
  margin-right: 10px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}
.form__agreement-mark::after {
  position: absolute;
  top: 13%;
  left: 12%;
  display: block;
  width: 7px;
  height: 5px;
  border: 1px solid #ffffff;
  border-right: none;
  border-top: none;
  transform: rotate(-45deg);
}

