.catalog {
  width: 100%;
  padding-top: 70px;
  padding-bottom: 140px;
  background-color: $black-pearl;

  @media (max-width:$tablet-width) {
    padding-bottom: 75px;
  }

  @media (max-width:$mobile-width) {
    padding-bottom: 85px;
  }
}

.catalog__container {
  max-width: calc($desktop-width-only - 700px);
  margin: 0 auto;

  @media (max-width:$tablet-width) {
    padding: 0 50px;
  }

  @media (max-width:$mobile-width) {
    padding: 0 25px;
  }

  h2 {
    font-family: $roboto-condensed;
    font-size: 36px;
    font-weight: 400;
    line-height: 51px;
    color: $color-default-white;
    margin: 0;
    margin-bottom: 50px;

    &:nth-child(even) {
      display: none;
    }

    @media (max-width:$mobile-width) {
      font-size: 28px;
      line-height: 39px;

      &:first-child {
        display: none;
      }

      &:nth-child(even) {
        display: block;
      }
    }
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 7%;

  @media (max-width:$tablet-width) {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.catalog__link {
  position: relative;
  font-family: $roboto-condensed;
  font-size: 20px;
  line-height: 30px;
  color: $color-default-white;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;

  p {
    margin: 0;
    position: absolute;
    grid-row: 1 / 2;
    max-width: 185px;
    margin-top: 50px;
    margin-left: 40px;

    @media (max-width: $tablet-width) {
      max-width: 150px;
    }

    @media (max-width: $mobile-width) {
      font-size: 18px;
      max-width: 135px;
    }
  }

  &:hover,
  &:focus {
    box-shadow: 0 -2px 5px $deep-sky-blue-accent;
  }

  &:active {
    box-shadow: 0 -2px 5px $whisper;
  }
}

.catalog__image {
  width: 100%;

  img {
    width: 100%;
  }
}

.catalog__link-button {
  width: 60px;
  height: 60px;
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: $deep-sky-blue;
  border: none;
  border-radius: 3px 0 0;

  @media (max-width:$mobile-width) {
    width: 40px;
    height: 40px;
  }

  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 6px;
    height: 6px;
    border: 1px solid $color-default-white;
    border-bottom: none;
    border-left: none;
    transform: rotate(45deg);
    left: 43%;
    top: 45%;

    @media (max-width:$mobile-width) {
      width: 4px;
      height: 4px;
    }
  }

  &:hover,
  &:focus {
    background-color: $whisper;

    &::before {
      border-color: $deep-sky-blue;
    }
  }

  &:active {
    opacity: .8;
  }
}
